$myColor: red;

$aqua:           #EBF2FF;
$blue:           #2979FF;
$white:          #FFF;
$black:          #000;
$text-light:     #787885;
$text-dark-grey: #54565A;
$text-primary:   #333;
$text-dark:      #19191D;
$grey-light:     #F6F8F9;
$grey-very-light:#E1E1E3;
$grey-medium:    #F0F0F1;
$pink:           #A42FF0;
$green:          #65B168;
$red:            #F6655A;

*{
  margin: 0;
  font-family: "Roboto", "Helvetica", "Arial", sans-serif;
}
body{
  padding-top: 3rem;
  font-size: 0.875rem;
  line-height: 1.312rem;
  font-weight: 400;
  color: $text-primary;
}
.MuiInput-underline:after, .Mui-focused.MuiInput-underline:after{
  border-bottom: none !important;
}
.MuiPickersDay-daySelected{
  background-color: $blue !important;
  .MuiTypography-body2{
    color: $white;
  }
}
.MuiTypography-colorPrimary { color: $blue !important;}
.MuiTypography-colorSecondary { color: $text-light !important;}
.MuiBackdrop-root {
  background-color: rgba(0, 0, 0, 0.3) !important;
}

.MuiFormControl-root{
  padding-top: .5rem !important;

  .MuiInputLabel-root{
    font-size: 1.125rem;
    font-weight: 400;
    line-height: 1.5rem;
    color: $text-light;
    letter-spacing: 0.4px;
    z-index: 9;
    
      &.Mui-focused{
        color: $text-light; // For Focused State Only
      }
  }

  .MuiInputBase-root{
    border-radius: .25rem;
    .MuiInputBase-input{
      height: 1.25rem;
      padding: .5rem;     //padding: .5rem .5rem .5rem 0;
      border-radius: .25rem;
      background: $white;
     // border-bottom: solid 1px #D6D6D6;
    }
  }

  .MuiInput-underline{
    &::before{
      display: none !important;
    }
  }
  &.MuiFormControl-marginNormal{
    margin-top: .75rem;
    margin-bottom: .5rem;
  }
}
.MuiButton-root{
  //padding: 0.187rem 0.937rem !important;
  text-transform: none !important;
  
  &.workspace-btn , &.later-btn{
    margin-top: 1.125rem;
  }
  &.MuiButton-outlinedPrimary{
    border:1px solid $blue !important;
    color: $blue !important;
  }
  &.MuiButton-outlinedSecondary, &.MuiButton-outlinedSecondary:hover {
    border-color: $text-light;
    color: $text-light;
    background-color: transparent;
    &.icon-btn-sm-rounded {
      border-radius: 50px;
      margin-left: .625rem;
      padding-top: 0;
      padding-bottom: 0;
      font-weight: 400;
    }
  }

  &.MuiButton-containedPrimary{
    background-color: $blue !important;

  }

  &.MuiButton-containedSizeLarge{
    padding: 0.812rem 1.375rem !important;
   }
   
}
//paper Shadow
.MuiPaper-elevation8 {
  box-shadow: 0 2px 6px #d0d0d0 !important;
}

//List Formatting Starts
.MuiList-root{
  outline: none;
  .MuiListItem-root{
    .MuiListItemText-root{
      font-size: 0.875rem;
      line-height: 1.875rem;
      color: $text-dark-grey;
    }
  }
}
//List Formatting Starts

//Select Menu Paper
.MuiMenu-paper{
  // border:solid 1px red;
   .MuiMenu-list{
     .MuiMenuItem-root{
      font-size: 0.8rem;
      line-height:1rem;
      color: $text-primary;
      padding-left: .5rem;
      padding-right: 1rem;
       .MuiSvgIcon-root{
         font-size: 1rem;
         color: $blue;
         margin-right: 0.625rem;
       }
     }
   }
 }
 

// Typography Formatting Starts
.MuiTypography-h1{
  text-transform: uppercase;
  font-weight: 900 !important;
  font-size: 2.5rem !important;  
  line-height: 3.437rem !important;
  letter-spacing: normal !important;
}
.MuiTypography-h2{
  font-size: 2.125rem !important;  
  font-weight: 700 !important;
  line-height: 3rem !important;
}
.MuiTypography-h3{
  font-size: 1.375rem !important;  
  font-weight: 500 !important; //400 in design system
  line-height: 1.625rem !important;
}
.MuiTypography-h4{
  font-size: 1.25rem !important; 
  font-weight:500 !important;  //400 in design system
  line-height: 2rem !important;
}
.MuiTypography-h5{
  font-size: 1.125rem !important;  
  font-weight: 700 !important;
  line-height: 1.5rem !important;
}
.MuiTypography-h6{
  font-size:1rem !important; 
  font-weight: 500 !important;  
  line-height: 1.187rem !important;  
  color:$text-primary;
}
.MuiTypography-subtitle1 {
  font-size: 1.125rem !important;    
  font-weight: 400 !important; //We kept it normal alternate of H5
  line-height: 1.75rem !important;
  color: $text-primary;
}
.MuiTypography-subtitle2 {   
  font-size: 0.875rem !important;    
  font-weight: 500 !important; 
  line-height: 1rem !important;
  color: $text-primary;
}
 
.MuiTypography-body1{  
  font-size:1rem !important; 
  font-weight: 400 !important;  
  line-height: 1.5rem !important;  
  color:$text-primary;
}

.MuiTypography-body2{
  font-size:0.875rem !important; 
  font-weight: 400 !important;  
  line-height: 1.187rem !important;  
  color:$text-primary;
}

.MuiTypography-overline{
  font-size:0.75rem; // 12px as per Design System
  font-weight: 500;
  line-height:1rem !important;
  text-transform: uppercase !important;
}

.MuiTypography-caption {
  font-size:0.75rem; // 12px as per Design System
  font-weight: 400;
  line-height:1rem;
  text-transform: none !important;
}
.MuiTypography-button{
  // Yet to define
}
 
.regular400{
  font-weight: 400 !important;
}
.bold500{
  font-weight: 500 !important; // Important only for MUI overwrite.
}
.bold600{
  font-weight: 600 !important; // Important only for MUI overwrite.
}
.bold700{
  font-weight: 700 !important; // Important only for MUI overwrite.
}
.bold900{
  font-weight: 900 !important; // Important only for MUI overwrite.
}
 
