// Table Formatting Starts
.MuiTableContainer-root{
  .MuiTable-root{
      .MuiTableHead-root{
        .MuiTableRow-head{
          .MuiTableCell-head{
            font-size: 0.875rem;
            line-height: 1.312rem;
            font-weight: 500;
            color: $text-primary;
            padding: 1rem 1rem 0 1rem;
            border-bottom: none; // As of now only for Pricing table, will add a sub class for future
            
              .MuiPaper-root{
                padding: 0.75rem;
                font-size: 1rem;
                font-weight: 500;
                line-height: 1.25rem;
                background:$grey-medium;
                box-shadow: none;
                position: relative;
                margin: auto;
                width: 7.25rem;
                cursor: pointer;

                &.active{
                  background: $pink;
                  color: $white;
                  cursor: default;
                  &::before{
                      content: "";
                      width: 0;
                      height: 0;
                      border-style: solid;
                      border-width: .5rem .5rem 0 .5rem;
                      border-color: $pink transparent transparent transparent;
                      position: absolute;
                      bottom: -.5rem;
                      left: 50%;
                      margin-left: -.5rem;
                  }
                }
              }
          }
        }
      }

      .MuiTableBody-root{
        .MuiTableRow-root{
          .MuiTableCell-body{
            font-size: 0.875rem;
            line-height: 1.312rem;
            font-weight: 400;
            color: $text-primary;
            padding: .85rem;
            vertical-align: top;
 
            &:first-child{
              padding-left: 0;
            }

            .MuiPaper-root{
              box-shadow: none;
              &.text-mute {
                 opacity: .4;
                 font-weight: 500;

                &.active{
                   opacity: 1;
                }
              }
            }

          }
        }
      }
  }
  + .MuiGrid-root{
    .table-footer{
      padding:2.5rem 0;
      button + button {
        margin-left: .5rem;
      }
    }
  }
}
// Table Formatting Ends


//Stepper Formatting Starts
.MuiStepper-root{
 //border:solid 1px pink;
  .MuiStep-root{
   // border:solid 1px red;
    .MuiStepLabel-root{
      .MuiStepIcon-root{
          &.MuiStepIcon-active,
          &.MuiStepIcon-completed{
              color: $green !important;
          
          }
          &.MuiStepIcon-completed{
            .MuiTypography-caption{
              color: $green !important;
            }
          }
      }

      .MuiStepLabel-label{
        &.MuiStepLabel-alternativeLabel{
          margin-top: .25rem;
          .MuiTypography-caption{
            color: $green !important;
          }
        }
      }

      .MuiStepLabel-iconContainer{
        svg{
         // color: $grey-very-light;
          width: 1.25rem;
          height: 1.25rem;

          circle {
            cx: 12;
            cy: 12;
            r: 12;
            //color: $text-primary;
          }
        }
      }
      &.Mui-disabled{
        .MuiStepLabel-label{
          &.MuiStepLabel-alternativeLabel{
            .MuiTypography-caption{
              color: $text-primary !important;
            }
          }
        }
      }
    }
  }
}
//Stepper Formatting Ends

.MuiBreadcrumbs-root{
  .MuiBreadcrumbs-ol{
    .MuiBreadcrumbs-li{
      .MuiLink-root, .MuiBreadcrumbs-separator{
        font-size:.75rem;
        font-weight: 400;
        line-height: 1rem;
        color: $text-primary;
      }
      .MuiTypography-body1{
        font-size:.75rem !important;
        font-weight: 400;
        line-height: 1rem;
        color: $text-light;
      }
      
    }
    li{
      &.MuiBreadcrumbs-separator{
        font-size:.75rem;
        font-weight: 400;
        line-height: 1rem;
      }
    }
  }
}


//For Sprint Plan component Formatting 
.project-board {
  display: flex;
  flex-direction: row;
  border: 1px solid #DADADA;
  background: #fff;
  border-radius: 5px;
  width:84%;
  margin: 0 1rem 2.5rem 1rem;
  &__left{
    border-right: solid 1px #DADADA;
    padding:0.375rem 0.25rem 0.375rem 0.875rem; 
    width: 17rem;

    .ttl{
        .MuiTypography-body2{
          font-weight: 500 !important;
          color: #333;
        }
        .MuiSvgIcon-root{
          font-size: 1rem;
        }
        .nonEdit, .editable{
          display: flex;
          border-bottom: 1px solid #B4B4BB;
          justify-content: space-between;
          .MuiInputBase-root{
            min-width:15.062rem;
            white-space: nowrap;
              .MuiInputBase-input{
              text-overflow: ellipsis;
              overflow: hidden;
              font-weight: 500 !important;
              color: #333;              
              padding: 0 2px;
              margin-right:3px;
              //border-right: 1px solid #B4B4BB;
            }
          }
          .MuiIconButton-root{
            padding: 4px;
          }
          .MuiInput-underline:hover:not(.Mui-disabled):before{
            border-bottom: 1px solid #B4B4BB;
          }
        }  
        .nonEdit{  
          border-bottom: 1px solid transparent;
        }

        .project-ttl{
          width: auto;
        }
      }

    .repo{
      justify-content: space-between;
      &__name{
        .g-name{
          font-size: .75rem;
          font-weight: 500;

          &:hover, &:focus{
            text-decoration: none;
          }
        }
        .repoName{
          color: #3A3A44 !important;
        }
        .MuiInput-underline{
          .MuiSelect-select{
            padding-top: 0;
            padding-bottom: 0;
            &:focus{
              background: #FFF;
            }
          }
          &::before, &::after{
            display: none;
          }
        }
        &.connected{
        
        }
      }
      &__utility{
       }
     }

    .avatar{
      display: flex;
      align-items: center;
      margin-left:1.75rem;
      margin-top: -0.25rem; // For positioning setup only
      .MuiAvatarGroup-root{
        display: inline-flex;
        margin-right: .5rem;
        .MuiAvatar-root{
          width: 1.25rem;
          height: 1.25rem;
          font-size:.75rem;
        }
      }
      .g-team{
        font-size: .75rem;
        line-height: .75rem;
        &:hover, &:focus{
          text-decoration: none;
        }
      }
    }
  }
  &__rgt{
    padding: .75rem 1rem;

    .sprint{
      .timeline {
          position: relative;
          padding: .5rem 1rem .5rem 0;
          .MuiTypography-subtitle2{
            color:#2979FF;
          }
          // .MuiTypography-body2.fontWeightMedium{
          //   font-weight: 500 !important;
          // }
         .MuiTypography-overline{
            font-weight: 600 !important;
            text-transform: none !important;
            color: #4A4B57;
            opacity: .3;
            padding-top: 0.375rem;
         }

         .MuiTypography-body2 {
            font-weight:500 !important;
            line-height: 1.5rem !important;
            color: #787885;

            .grey-text{
              color: #787885;
            }
          }

          .MuiButton-outlinedSizeSmall{
            padding: 0px 0.75rem;
            background:$white;
            &.MuiButton-outlinedPrimary{
              border: 1px solid #2979FF !important;
              font-weight: 400;
            }

          }

          .sprint-form{
            border: 1px solid #2979FF;
            border-radius: 4px;
            position: relative;
            width: 16rem;
            background: $white;
            .MuiFormControl-root{
              padding-top: 0 !important;
              .MuiInputBase-root{
                .MuiInputBase-input{
                  height: 1.25rem;
                  padding:.125rem .35rem;
                  background: #ffffff;
                  color: #2979FF;
                  font-size: .8rem;
                  font-weight: 400;
                  em{
                    font-style: normal;
                  }
                }
                .MuiSelect-icon{
                  color: #2979FF;
                }
              }
              &.MuiFormControl-marginNormal{
                margin: 0;
                max-width: 7rem;
                .MuiInputBase-root{
                  .MuiInputAdornment-root{
                    .MuiButtonBase-root{
                      padding: .25rem;
                      .MuiIconButton-label{
                        color: #2979FF;
                        .MuiSvgIcon-root{
                          font-size: 1.125rem;
                        }
                      }
                    }
                  }
                }
              }
             
              &:first-child{
                min-width: 7rem;
                &::after{
                  content: "";
                  right: 0;
                  top: 4px;
                  height: calc(100% - 8px);
                  width: 1px;
                  background: #C4C4C4;
                  position: absolute;
                }
              }
            }
            .MuiButton-root {
              position: absolute;
              right: -1px;
              top: -1px;
              padding: 0.080rem .25rem;
              border-top-left-radius: 0;
              border-bottom-left-radius: 0;
              min-width: 2rem;
              box-shadow: none;
            }
          }

         &::after{
           content: "";
           position: absolute;
           left: 0;
           top: 0;
           height: 5px;
           width: 100%;
           background: #E1E1E3;;
         }

         &::before{
            content: "";
            position: absolute;
            left: 0;
            top: -0.125rem;
            height: .5rem;
            width: .5rem;
            border-radius: 50%;
            background: #B4B4BB;
            z-index: 99;
         }

         &.active{

            .MuiTypography-overline{
              //font-size: 0.75rem !important;
              color: #333;
              opacity: 1;
            }
            .MuiTypography-body2 {
              color: #2979FF;
              &.grey-text{
                color: #787885;
              }
            }
            &::before{
                top: -0.175rem;
                height:0.625rem;
                width:0.625rem;
                background: #2979FF;;
            }
         }

         &.sprint__retro{
           &::after{ display: none;}
         }
      }
    }
  }
}





