// For Snackbar / Toaster /Alerts Message Formatting
.alerts{
    &.toaster-alerts{
      .MuiAlert-root{
        width:600px;  
        margin: 1rem; 
      }
      .MuiAlert-outlinedSuccess{
        background-color: #EDF6EE;         
        color:$green;
      } 
    }
    &.simple-alerts{
      .MuiPaper-root{
        box-shadow: none;
      }
      .MuiAlert-filledSuccess{
        background-color: #EDF6EE;  
        color:#2B662E;
        font-weight: normal;
      }
    }
    .MuiAlert-outlinedInfo.customAlert{
      border-color: $white;
      background-color: $white;
      .MuiAlert-message{
        display: flex;
        align-items: center;
        justify-content: space-between;
        width: 100%;
        .MuiSvgIcon-root{
          color: $text-light;
          margin-right: 8px;
        }
      }
    }
    .MuiSnackbar-anchorOriginBottomCenter{
      bottom: 42%;
    }
  }


//Login form & Authoroze with
.form-container{
    margin-top: 8.187rem;
    min-width: 28.437rem;
    .form-desc{
      width: 23rem;
    }
    .loginForm{    
      max-width: 22.5rem;
      margin-top: 2.5rem;
      text-align: left;
      display: block;
      .MuiFormControl-root{
        display: block;
        .MuiInputBase-root{
            border-radius: 2px !important; 
            font-size: 0.875rem !important;
            border: 1px solid #B4B4BB !important;
            margin-top: 0.937rem;
            width: 100%;
            &.Mui-error{
              border: 1px solid red !important;
            }
          }
          .MuiInputBase-input{
              background-color: #fff !important;
              padding: .5rem .5rem;
          }
        }
      .MuiFormControlLabel-root{
        .MuiCheckbox-root{
          padding:4px;
          margin:0.187rem 0.187rem 0.187rem 0.562rem;
            &:hover{
              background-color:transparent !important; 
              transition: none !important;
            }
            &.Mui-checked{
              .MuiSvgIcon-root{
                color: $blue;
              }
            }
            .MuiSvgIcon-root{
                width: 1.125rem;
                height: 1.125rem;
            }
          }
        .MuiTypography-body1{
            font-size: 0.75rem !important;
        }
      }
      .MuiIconButton-root{
        padding: 0.187rem 0.375rem;
        .MuiSvgIcon-root{
            width: 1.25rem;
        }
      }
    }
  }

// .MuiFormControl-root + .MuiFormControl-root {
//   margin-top:1.125rem;
// }

.authorizeWith{
  width: 22.5rem;
    .authorizeText{
      position: relative;
      font-size: 0.625rem;
      color: #333;
      font-weight: 700;
      letter-spacing: 0.093rem;
      &:before, &:after{
        content: "";
        width: 80%;
        display: inline-block;
        position: absolute;
        right: calc(50% + 70px);
        border: 1px solid #E1E1E3;
        top:45%;
      }
      &:before{
        right: 10%;
        left: calc(50% + 70px);
      }
    }
   .authorizeBtn{
    .MuiButton-label{
      display: flex;
      justify-content: space-evenly;
      align-items: center;
    }
      button{
          width: 7rem;
          border: 1px solid #B4B4BB;
          .MuiIcon-root{
            width: 100%;
            height: auto;
            display: contents;
            min-height: 2.5rem;
          }
      }
      button + button{ margin-left: 0.75rem;}
  }
  .MuiLink-button{
      font-weight: bold;
  }
}

.centered-div{
  position: absolute;
    width: 100px;
    height: 50px;
    top: 50%;
    left: 50%;
    margin-left: -50px; /* margin is -0.5 * dimension */
    margin-top: -25px; 
}

