@import '../../App.scss';
 // Landing Page Starts 
 .trial-container{
    padding: 2.25rem 2.25rem;
    background: $aqua;
    &__left{
      padding-right: 3rem;
      img{
        box-shadow: 0px 0px 60px rgba(0, 0, 0, 0.2);
        max-width: 100%;
      }
    }
    &__right{
      padding-left: 3rem;
      display: flex;
      flex-direction: column;
      justify-content: center;
      height: 100%;
      .cta{
        margin-top: 2.625rem;
      }
    }
   
 }

 .scrum-work-container{
  padding:2.625rem 2.25rem;
