@import '../../App.scss';

 .sprint-container{
    background: #F9F9FA;
    margin-top: .5rem;
    padding: 1rem 0;

    .project-board {
        border: 1px solid transparent;
        background: transparent;
        width: 100%;
        margin: 0;
        &__left{
            width:21.875rem;
            padding-left: 0;

            .ttl{
                background: $text-light;
                padding: .125rem .5rem;
                display: inline-block;
                position: relative;
               

                .MuiTypography-body2{
                    color: $white;
                    font-size: .75rem !important;
                    max-width: 4rem;
                    white-space: nowrap;
                    overflow: hidden;
                    text-overflow: ellipsis;
                    margin-top: .15rem;
                }

            }
            .ttl.has-background{
                &::after{
                    width: 0;
                    height: 0;
                    border-style: solid;
                    border-width: 0.85rem 0 0.9rem 0.85rem;
                    border-color: transparent #787885 transparent #787885;
                    position: absolute;
                    right: -13px;
                    top: 0;
                }
            }
           
            .avatar {
                margin-left: 6rem;
                margin-top: 0;
            }

        }
        
    }
}
.backlog{
    //border:solid 1px red;
    height: calc( 100vh - 216px);
    &__left{
        border-right:2px solid #D2D2D6;
        .action-btns{
            .MuiIconButton-root{
                padding: 5px;
            }
        } 
        .userStory{
            .MuiInputBase-root{
                .MuiInputBase-input{
                    background: $white;
                }
            }
        }       
    }
    &__rgt{
         min-width: 23.437rem;
        //  .normal-text{
        //      color: #D2D2D6;
        //  }
        }
}