@import '../../App.scss';

.d-none{
    display: none !important;
  }
  
.dashboard-outr{ background: #F8F8F8; padding: 1rem 0;} //height: calc(100vh - 4rem); 
  //KPI Cards Component
  .dashboard-main{
    margin: 0 1rem;
    .dashboardTitle{
      .MuiTypography-h3{
        margin: 0.625rem 0;
      }
    }
    .linkText{
      color: $blue;
      text-decoration: underline;
    }
    .kpiCards-main{
      margin-top: 0.937rem;
      .kpi-cards{ 
        padding: 0 5px;
        .noPadding{
          padding:0 !important;
        }
        // .MuiTypography-body1{
        //   color: $text-dark;
        //   font-weight: bold !important;
        // }
        &.kpi-cards-first{
          padding-left:0;
        }
        &.quick-card{
          padding-right:0; 
          .inner{
            justify-content: space-between;
            align-items: flex-start;
            .expand-icon{
              padding: .25rem 0;
            }
            // .MuiTypography-caption {
            //     font-size: 0.625rem;
            //      line-height: 1rem;
            //  }
          }     
        }
        > .MuiBox-root{
          width:100%;          
          border: 1px solid #D2D2D6;
          padding: 0.25rem  0.5rem;
        }
      }
      .MuiAvatar-colorDefault {
        color: $text-dark;
        background-color: #D2D2D6;
        font-weight: bold;
        width: 1.625rem;
        height: 1.625rem;
        font-size: 0.75rem;
      }      
      .text-more{
        height:auto;
        display: -webkit-box;
        -webkit-line-clamp: 3;
        -webkit-box-orient: vertical;
        line-height: .75rem;
        font-size: .7rem;
        // .MuiTypography-caption{
        //   line-height: .75rem;
        //   font-size: .7rem;
        // }
      }
      // .MuiTypography-body2{
      //   color: $text-primary;
      //   border:solid 5px red;
      // }
      .border-color{
        border-right: 1px solid #D2D2D6;
      }
    }    
    .kpiButtons{
      padding: 0.625rem 0;
      display: flex;
      align-items: center;
      .MuiButton-contained{
        background-color: $white;
        color: $blue;
        box-shadow: 0 2px 6px #d0d0d0;
        &:hover {
          background-color: $white;
        }
        .icon-plus{
          font-size: 0.687rem;
          padding: 0 0.50rem 0 0;
        }
      }
      .filterButton{
        padding: 0;
        font-size: 1rem;
        font-weight: normal;
        color: $text-primary;
        &:hover{
          background-color:transparent;
        }
        .MuiIconButton-root{
          padding: .125rem;
          margin: 0 .25rem;
        }
        
      }
    }
  }
  



.quickNotes{
  display: flex;
  border: 1px solid #DADADA;
  background: #fff;
  border-radius: 5px;
  margin: 0 1rem 2.5rem 0;
  width: 16%;
  .quickNote-card{
    width: 100%;
    box-shadow: none;
    .MuiCardContent-root{
      padding:0.625rem;
      .quickNote-textfield{
        padding:0 !important;
        width: 100%;
        .MuiInputBase-root{
          padding:0;
          .MuiInputBase-input{
            background-color: $white;
            font-size:0.875rem;
            padding:0;
          }
        }
      }
    }
    .MuiCardActions-root{
      justify-content: flex-end;     
      padding:0 0.625rem; 
      .MuiButton-textPrimary{
        color: $blue;
      }
    }
  }
}


#teamMemberMenu{
  .MuiPopover-paper{
    width: 12.5rem;
    padding:0.625rem 0.937rem;
    margin: 2.5rem 1rem 0 0;
    .MuiList-padding{
      padding:0;
    }
    .MuiMenuItem-root{
      padding:0.312rem 0;
      .MuiTypography-caption{
        color: $text-light;
      }
      // .normal-text{
      //   font-weight: 500;
      // }
    }
  }
}


#actionMenu{
  .MuiPopover-paper{
    margin-top: 2.5rem;
    margin-left: -10.5rem;
    .MuiMenuItem-root{
      font-size: 0.875rem;
    }
    .textDanger{
      color: $red;
    }
  }
}

//github check

.selectCheck{
  position: relative;
  .selectCheckIcon{
    position: absolute;
    bottom: -4px;
    right: 23px;
  }
}