@import '../../App.scss';

 .sprint-container{
    background: #F9F9FA;
    margin-top: .5rem;
    padding: 1rem 0;

    .project-board {
        border: 1px solid transparent;
        background: transparent;
        width: 100%;
        margin: 0;
        &__left{
            width:21.875rem;
            padding-left: 0;
            .MuiBreadcrumbs-ol{
                align-items: baseline;
            }
            .ttl{
                background: $text-light;
                padding: .125rem .5rem;
                //display: inline-block;
                //position: relative;
                display: flex;
                position: relative;
                align-items: center;
               

                // .MuiTypography-body2{
                //     color: $white;
                //     font-size: .75rem !important;
                //     max-width: 4rem;
                //     white-space: nowrap;
                //     overflow: hidden;
                //     text-overflow: ellipsis;
                // }

            }
            .ttl.has-background{
                   .MuiTypography-body2{
                    color: $white;
                    font-size: .75rem !important;
                    max-width: 4rem;
                    white-space: nowrap;
                    overflow: hidden;
                    text-overflow: ellipsis;
                    margin-top:0;
                    }
                &::after{
                    content: "";
                    width: 0;
                    height: 0;
                    border-style: solid;
                    border-width: 0.85rem 0 0.9rem 0.85rem;
                    border-color: transparent #787885 transparent #787885;
                    position: absolute;
                    right: -13px;
                    top: 0;
                }
            }
           
            .avatar {
                margin-left: 6rem;
                margin-top: 0;
            }

        }
        
    }
}
.kanban{
    border-top:solid 1px #D6D6D6;
    border-bottom:solid 1px #D6D6D6;
    height: calc( 100vh - 216px);
   

    &__box{
        position: relative;
        .kanban__board{
            position: relative;
            &.active{
                box-shadow:#e0e0e0 5px 5px 5px;
            }
       }
    }

    &__board{
        border-right: solid 1px #D6D6D6;
        padding: .5rem;
        .MuiIconButton-root{
            color: #333;
        }
        &.disabled{
            filter: blur(8px);
             -webkit-filter: blur(3px);
             pointer-events: none;
        }
        &:last-child{
            border-right: none;
        }
    }
       
    &__card{
        border: solid 1px $text-light  !important;
        //padding: .5rem;
    }

    &__form{
       // border:solid 1px red;
        .MuiFormControl-root{
             .MuiInputBase-input{
                background: transparent;
                border-bottom: solid 1px #D6D6D6;
                border-radius: 0 !important;
                padding-left: 0 !important;
             }
        }
    }

     
    &.disabled{
        filter: blur(8px);
         -webkit-filter: blur(3px);
         pointer-events: none;
    }
}

.kanbanSprintRow{
    padding:0.625rem 2.5rem 1.25rem;
    background:$white;
    box-shadow: 0px 4px 4px rgba(0, 0, 0, 0.15);
    .astrick{
        color: #f44336;
    }
    .MuiInputLabel-root{
        font-size: 1rem;
        font-weight: bold;
    }
   .MuiFormControl-root{
        .MuiInputBase-root{        
            border-bottom: 1px solid #B4B4BB;
            border-radius: 0 !important;
            .MuiInputBase-input{
                background-color: #fff !important;                    
                font-size: 0.875rem;
                padding: .4rem 0 .4rem 0;
            }
        }
    }
    .MuiFormControl-root.MuiFormControl-marginNormal {
        margin-top: 0.5rem;
        margin-bottom: 0;
    }
    .kanbanDatepicker{
        .MuiInputBase-root{
            .MuiInputBase-input{
                min-width: 74px;
            }
        }
    }
}

  
.kanbanSprintselected{
    //margin:1rem 0;
    padding:0.5rem 0;
    //border-top: 1px solid #D2D2D6;
    //border-bottom: 1px solid #D2D2D6;
    // .MuiTypography-body2{
    //     color: #333;
    // }
    .MuiFormControl-root {
        padding-top: 0 !important;
        min-width:7.5rem;
    }
    .MuiInputBase-root{
        .MuiInputBase-input{
            background-color: #fff !important;
            //border: 1px solid #B4B4BB;
            border-radius: 3px;        
            font-size: 0.875rem;
            padding: 0.4rem;
            color: $blue;
            font-weight: bold;
        }
    }
    .MuiSelect-icon {
        //top: calc(50% - 15px);
        color: $blue;
    }
    
}

