@import '../../App.scss';
.onboard-container{
    margin-top: 1.5rem;
    .org-board{
        margin-top: 1.25rem;
        .logo-upload{
          padding: 0;
          margin-bottom: .5rem;
        }
    }

    .org-git{
        .btnText{
            font-weight: 500;
            letter-spacing: 0.02857em;
        }
        .org-name{
            color:$text-dark-grey;
            font-weight: bold;
        }
        
        a{
            padding-left:0.25rem;
            color: $blue;
            text-decoration: none;
            font-size: 0.875rem;
        }
         
        .upload-logo{
            //height: 7.5rem;
            //width: 7.5rem;
            //border-radius: 50%;
            position: relative;
            background-color: #FFF;
            //margin: .75rem 0;

            .org-logo{
                //height: 7.25rem;
                width: 7.25rem;
                //border-radius: 50%;
                position: relative;
                background-color: $white;
                //margin: .75rem 0;
                
                img{
                    width: 100%;
                    height: 100%;
                    //max-width: 100%;
                    //border-radius: 50%;
                    position: static;
                    &.imgShadow{
                        box-shadow: 0px 0px 8px rgba(0, 0, 0, 0.25);
                    }
                }
            }
            .camera-btn{
                position: absolute;
                right: 0.35rem;
                bottom: 0.35rem;
            }
        }

        .git-setup-text{
            margin: 1.5rem 0 .75rem 0;
        }
    }

    .org-success{
        margin-top: 7rem;

        .MuiGrid-root{
            .MuiBox-root{
                margin-bottom: .5rem;

                .git-logo{
                    border-radius: 50%;
                    height:7.375rem;
                    width:7.375rem;
                    position: relative;
                  }
                  .tick{
                    position: absolute;
                    right: 0;
                    top: 0;
                    background: $green;
                    width: 2rem;
                    height: 2rem;
                    color: $white;
                    border-radius: 50%;
                    line-height: 2rem;
                  }
            }
        }

        // .MuiTypography-body1{
        //     margin-bottom: 2rem;
        // }
    }
}

.onboard-container, .organization-container, .success-container{
    .MuiTypography-h4{
        font-weight: normal !important;
    }
    .MuiButton-contained{
      background-color: $blue;
    }
    .MuiStepIcon-root{
      &.MuiStepIcon-active{
      color: $green;
      }
    }
    .MuiStepLabel-label{
      &.MuiStepLabel-active{
          .MuiTypography-caption{
            color: $green;
            font-weight: bold !important;
        }
      }
    }
    .MuiFormControl-root{
        .MuiInputBase-root{
            .MuiInputBase-input{
                background: #F0F0F1;
                }
            }
        }
    }
 