@import '../../../App.scss';

// Tabs Formatting Starts
.MuiGrid-root{
    .MuiTabs-root{
      .MuiTabs-scroller{
        .MuiTabs-flexContainer{
          .MuiTab-root{
            font-size: 1rem;
            text-transform: none;
              @media (min-width: 600px) { 
                 min-width: 100px;
                }
  
                &.Mui-selected{
                    color: $blue;
                    &::before{
                    content: "";
                    position: absolute;
                    bottom: 0px;
                    left: 0;
                    width: 100%;
                    border-top-left-radius: 5px;
                    border-top-right-radius: 5px;
                    height: 4px;
                    background: $blue;
                    display: none !important;
                        }
                    }
                }
            }
        }
    }
}

