@import '../../App.scss';

 .product-container { padding-right: .5rem !important;} 
.ds{ //Short Class For Daily Scrum 
     height: calc( 100vh - 216px);
    &__left{
        border-right:2px solid #D2D2D6;
        .action-btns{
            .avatar{
                display: flex;
                align-items: center;
                
                .MuiAvatarGroup-root{
                    display: inline-flex;
                    margin-right: .5rem;
                    .MuiAvatar-root{
                        width: 1.25rem;
                        height: 1.25rem;
                        font-size:.75rem;
                    }
                }
                
            }
        } 
        
        
        .MuiInputBase-root{
            border-radius:0;
            .MuiInputBase-input{
                background:$white;
                border-radius:0 !important;
                border-bottom:solid 1px $grey-medium;
                paddi   : .5rem .5rem .5rem 0 !important;
            }
        }

        .contentEditor{
            background:  $white;
            padding: .75rem;
            border-radius: 6px;
            border:solid 1px $grey-medium;
            h6{
                font-size: 1rem !important;
                font-weight: 500 !important;
                line-height: 1.187rem !important;
                color: #333;
                margin: .5rem 0;
            }
            ul{
                margin: 0;
                padding: 0 0 0 1rem;
                li{
                    padding:.125rem  0;
                    margin: .5rem 0;
                    list-style: square;
                    border-bottom:solid 1px $grey-light;
                }
            }

            &:focus,&:hover{
                outline: none;
            }
        }
       


        .preview{
            background: #F2F2F2;
            padding: 1rem 1.5rem;
            &__header{
                opacity: .4;
                cursor: not-allowed;
            }
            &__body{
                opacity: .4;
                cursor: not-allowed;
               // border:solid 1px red;
                .MuiTypography-body2{

                }
                .scrum-field{
                    min-height: 1.5rem;  //49px as per UI
                    position: relative;
                    .MuiFormControl-root{
                        display: none;
                        padding-top: 0 !important;
                    }

                    &::before{
                        content: "";
                        position: absolute;
                        left: 0;
                        top: .25rem;
                        width: .35rem;
                        height: .35rem;
                        background: #3A3A44;
                        border-radius: 50%;
                    }
                }
            }
            &__footer{
                
            }

            &.active{
                width: 100%;
                padding:1rem .25rem;
               // border:solid 1px red;
                background: $white;
                margin-top: 2rem;
                box-sizing: border-box;
                .preview__header{
                    //opacity:1;
                    display: none;
                }
                .preview__body{
                    opacity: 1;
                    cursor:default;
                    .scrum-field{
                        margin-top: 0;
                        margin-bottom: 1.25rem;
                        .MuiFormControl-root{
                            display: block;
                            width: 100%;

                            .MuiInputBase-root{
                                width: 100%;
                            }
                        }
                        &::before{
                            display: none;
                        }
                    }    
                }
                
                .preview__footer{
                    display:none;
                }
            }
        }
    }
    &__rgt{
         min-width:2rem;
         position: relative;
         .MuiDrawer-docked{
            .MuiDrawer-paper{
                position:absolute;
                right: 0;
                left: auto;
                border:none;
            }
         }
    }
}
