$white:          #FFF;
$text-light:     #787885;


.MuiAvatar-root.userProfile{ 
    width:120px; height:120px;
}


.User {
   

 svg {
    position: absolute;
    bottom: -6px;
    color:$white;
    height: auto;
    right: 5px;
    cursor: pointer;
    width: 1.25rem;
    background: $text-light;
    border-radius: 50%;
    padding: 8px;
}
}

.wideSeperator.MuiDivider-root{
    width: 100%;
    margin-top:12px;
    margin-bottom: 12px;
}
.org{align-self: flex-start;}
.repo-list {
    
    line-height: 0.75rem;

}
.light-text{
    font-size:0.625rem;
    color:$text-light;
}
.form-box{ 
  

    .MuiFormControl-root .form-input input{
        background: $white;
        border: 1px solid #B4B4BB;
        padding: 5px;
    }
    .text-area{ 
        margin-top:15px;
        background: $white;
        border: 1px solid #B4B4BB;
    }
}
.cursor-pointer{ 
    cursor: pointer;
}