@import '../../App.scss';


.projectLink{
    position: relative;
    border:1px solid #E1E1E3;
    border-radius: 6px;
    width: auto;
    .MuiInputBase-root{
        width: 100%;
        background: #FFF;
        border-radius: 0.312rem;
      .MuiInputBase-input{
        font-size: .875rem;
        color: $blue;
      }
    }
    .MuiIconButton-root{
        border-radius: 0.125rem;
        font-size: 0.875rem;
        padding: 0.312rem;
        background: #F0F0F1;
        border: 1px solid #E1E1E3;
        &:hover{
            border-radius: 0;
        }
        .MuiSvgIcon-root{
            font-size: 1.10rem;
            margin-right: 0.2rem;
        }
    }
}
.MuiDialogActions-root{
    padding: 0.5rem 1.5rem 1.125rem !important;
}
.recipients-container{
    border: 1px solid #E1E1E3;
    border-radius: 0.187rem;
    padding:0.625rem;
    min-height: 7rem;
    .MuiChip-root{
        background: #F0F0F1;
        border-radius: 0.187rem;
        .MuiChip-deleteIcon{
            color: $blue;
        }
    }
    .MuiChip-root + .MuiChip-root{
        margin-left:0.312rem;
    }
}