@import '../../App.scss';

.d-none{
    display: none !important;
}
.addDialog{
    .MuiDialog-paperFullWidth {
        min-height: 95vh;
    }
}
.MuiDialogTitle-root{
padding: .25rem 1.25rem 0 1.25rem !important;
.dialog-title{
    display: flex;
    justify-content: space-between;
    }
}

 .dialogStepper{
    .stepperSM{
        align-items: center; 
        flex-direction: row;
        width: 25%;
        margin: 0 auto;
        padding:0;
    }
}

.repo-search{
      &__panel{
          background:#EBEBEB;
          padding: .75rem 0.875rem;
          position: relative;
          width: 100%;
          .MuiInputBase-root{
              width: 100%;
              background: #FFF;
              border-radius: 0.312rem;
              padding: .125rem 2.5rem .125rem .5rem;
              font-size: 0.875rem;
          }
         .MuiIconButton-root{
            position: absolute;
            right: 1.25rem;
            top: 0.85rem;
            font-size: 1rem;
            line-height: 1.5rem;
            padding: .25rem .5rem;
        }
      }
    &__btns{
        .MuiButton-contained.Mui-disabled{
            color:#fff;
            background-color: $grey-very-light !important;
        }
        button + button { margin-left: 0.625rem;}
    }
  }

.addTeam{
      border-bottom: 1px solid #CCCCCC;
  }

.team{
    justify-content: space-between;
    margin-bottom: 3rem;
    &__info{
        // .MuiTypography-caption, .normal-text{
        //     line-height:1rem;
        // }
    }
    &__search{
        position: relative;
        border:1px solid #E1E1E3;
        border-radius: 6px;
        width: 17rem;
        .MuiInputBase-root{
            width: 100%;
            background: #FFF;
            border-radius: 0.312rem;
            padding: .125rem 2rem .125rem .5rem;
          .MuiInputBase-input{
            font-size: .875rem;
          }
        }
       .MuiIconButton-root{
          position: absolute;
          right: 0.2rem;
          top: 0.2rem;
          font-size: 1rem;
          line-height: 1.5rem;
          padding: .25rem;
      }
    }
    .MuiTableContainer-root{
        width: auto;
        padding:0 0.625rem 0.625rem 0.625rem;
        .team-member-table{
            padding:0 0.625rem 0.625rem 0.625rem;
            .MuiTableRow-head{
            border-bottom: 1px solid #CCCCCC;
            }
            .teamAvatar{
                height: 1.5rem;
                width: 1.5rem;
            }
            .MuiTableHead-root{
                .MuiTableRow-root{
                    .MuiTableCell-head{
                        padding: .5rem .5rem 0 .5rem ;
                        &:first-child{
                            padding-left: .5rem;
                        }
                    }
                }
            }
            .MuiTableBody-root{
                .MuiTableRow-root{
                    padding-top: .5rem;
                    .MuiTableCell-body{
                         padding: .5rem  .5rem  0  .5rem;
                    }
                    .MuiTableCell-body:first-child{
                        padding-left: .5rem !important;
                    }
                }
            } 
            .MuiTableCell-root{
                border-bottom: none;
            }
        }
    }
    
     
}

.repo-table{
    .MuiTableRow-head{
        border-bottom: 1px solid #CCCCCC;
        margin-bottom: 0.75rem;
    }
    .MuiTableBody-root{
        .MuiTableRow-root{
            &:hover{
                background: $grey-very-light;
            }
            .MuiTableCell-body{
                padding:0.625rem 0 !important;
                &:first-child{
                padding-left: 0.5rem !important;
                }
            }
        }
    } 
     .MuiTableCell-head{
        padding:0 0 0.5rem !important;
        &:first-child{
            padding:0 0.5rem 0.5rem !important;
        }
    }
    .MuiTableCell-root{
        border-bottom: none;
    }
    .MuiRadio-root{
        padding:0;
    }
    .MuiSvgIcon-root{
        width: 1.125rem;
        height: 1.125rem;
    }
}