@import "../../App.scss";

.section-title {
    .MuiTypography-body2 {
        color: #3A3A44;
    }    
    svg {
        margin-right: 22px;
    }    
}
.grey-container {
    background-color: #F0F1F9;
    padding: .75rem;
    border-radius: 6px;
    margin-bottom: .75rem;
    margin-left: 45px;
    fieldset {
        padding-top: 0 !important;
        .MuiTypography-body1 {
            color: $text-light;
        }
    }
}
.MuiTypography-body2 {
    line-height: 1.75rem !important;
}
.app-name {
    margin-left: 1rem !important;
}
.setting-page {
    padding-top: 1.75rem;
}
.page-title {
    border-bottom: 1px solid $grey-very-light;
    margin-bottom: 1rem;
    .MuiTypography-subtitle2 {
        line-height: 1.75rem !important;
    }
}
.width-32 {
    width: 32%;
}
.side-panel {
    background: url(/public/images/side-bg.png) no-repeat;
    background-size: cover;
}
.user-image {
    width: 8.5rem;
    height: 8.5rem;
    margin: 3rem auto 0;
    background: #F9F9FA;
    box-shadow: 0px 4px 4px rgba(0, 0, 0, 0.25);
    border-radius: 50%;
}
.row-wrap {
    margin-bottom: 0.375rem !important;
}
.app-row-wrap {
    margin-bottom: .75rem;
}
