@import '../../App.scss';

// Header Component Starts
.header{
    padding: .5rem 1rem 0 1rem;
    box-shadow: 0px 4px 3px rgba(0, 0, 0, 0.02);
    width: 100%;
    min-height: 3rem;
    background: $white;
    position: fixed;
    top: 0;
    left: 0;
    z-index: 999;
    overflow: auto;
   
    .logo{
      display: flex;
      flex-direction: row;
      align-items: flex-start;
      justify-content: start;
      height: 100%;
  
      a{
       display: flex;
       align-items: center;
       text-decoration: none;
         img {
           margin-right:.5rem;
         }
      }
   
      &__text{
         font-size: 1.125rem;
         line-height: 2.25rem;
         color: $black;
         font-weight: bold;
         text-decoration: none;
      }

      .logo-devider{
        position: relative;
        background: url(./devider.svg) left center no-repeat;
        width: .75rem;
      }

      &.emerged , &.with-org{
        align-items: center;
       a{
        > img:first-child { width: 1.25rem; height:1.25rem;}
       }
      }
    }
   
    .cta{
      display: flex;
      flex-direction: row;
      align-items: flex-start;
      justify-content: flex-end;
      height: 100%;
      padding-right: 1.5rem;
      padding-top: .25rem;
      .MuiButton-outlinedPrimary{
        min-width: 6.25rem;
       } 
      button + button { margin-left: 0.625rem;}
  
    }

    .user-panel{
      display: flex;
      justify-content: flex-end;
      align-items: center;

      .repo-search{
        border:2px solid #D2D2D6;
        border-radius: 6px;
        width: 17rem;
        display: flex;
        justify-content: space-between;
        padding: 0 .25rem;
        //background: $grey-light;
        .icon-search{
          font-size: 0.9375rem;
          padding: 0.5rem;
        }
        .MuiInputBase-root{
          padding: 0 .25rem;
          width: 17.812rem;
          .MuiInputBase-input{
              font-size: 0.812rem;
            }
          }
        }
   
      .notification-box{
        width: 2.5rem;
        height: 2.5rem;
        background: #EBF2FF;
        border-radius: 50%;
        border:none;
        margin: 0 1rem;
        .MuiIconButton-root{
          font-size:1.25rem;
          padding: 10px 12px;
        }
      }
   
      .user-avatar{
       display: flex;
       flex-direction: row;
       align-items: flex-start;
       justify-content: flex-end;
       height: 100%;
       padding-right: 1.5rem;
       cursor: pointer;
      }
    }
   
  }
  // Header Component Ends
  a.help-link {
    font-size: 0.875rem;
    line-height: 1.75rem;
    color: #2F281E;
    padding: 0rem .5rem;
    text-decoration: none;
    border-right: 1px solid #E1E1E3;
    margin-right: .75rem;
    font-weight: 500;
    margin-top: .35rem;
 }

 .user-menu{
   margin: 2rem 0 0 0;
 }
 .nagivationCustom{
  .MuiTab-root{
    font-weight: normal;
  }
 }

 #notification{
  .MuiPopover-paper{
    margin-top: .5rem;
    margin-left: -1.5rem;
    .MuiList-padding {
      padding: 0.625rem 1rem;
      .MuiMenuItem-root{
        padding:0.625rem 1rem 0.625rem 0.625rem;
        margin-right: 1rem;
      }
    }
  }
}

